import { pipe }   from 'rxjs';
import { filter } from 'rxjs/operators';

import { 
  select, createFeatureSelector,
  createSelector, MemoizedSelector
} from '@ngrx/store';

import { routerSelectors }        from '../router';

import { iApplicationConfigState } from './state';

export const selectApplicationConfig  = createFeatureSelector<any> ('applicationConfig');

let selectLaunched = (state: iApplicationConfigState) => {
  return state.launched;
};

export const selectApplicationConfigLaunched = createSelector(
  selectApplicationConfig,
  selectLaunched,
);


let selectResourceType = (state: iApplicationConfigState) => {
  return state.resourceType;
};

export const selectApplicationConfigResourceType = createSelector(
  selectApplicationConfig,
  selectResourceType,
);

let buildResourceById = (resourceType, routeParamId) => {
  let idParam = {
    id: routeParamId
  }
  return Object.assign({}, resourceType, idParam);
}

/*
{
  feature: 'sunray',
  type: 'companies',
  id: 57
}
*/

export const selectApplicationConfigResourceById = createSelector(
  selectApplicationConfigResourceType,
  routerSelectors.selectRouteParamId,
  buildResourceById,
)

let selectPrimaryEntity = (state: iApplicationConfigState) => {
  return state.primaryEntity;
};
export const selectApplicationConfigPrimaryEntity = createSelector(
  selectApplicationConfig,
  selectPrimaryEntity,
);

export const applicationConfigSelectors = {
  launched: selectApplicationConfigLaunched,
  resourceType: selectApplicationConfigResourceType,
  resourceById: selectApplicationConfigResourceById,
  primaryEntity: selectApplicationConfigPrimaryEntity,
}
