import { routerReducer } from '@ngrx/router-store';

import {
  applicationConfigReducer,
} from './slices/application-config/reducer'

import {
  applicationConfigInitialState,
} from './slices/application-config/initial-state'

import {
  systemComponentsReducer,
} from './slices/system-components/reducer'

import {
  systemComponentsInitialState,
} from './slices/system-components/initial-state'

export interface RootState {
  applicationConfig?: any,
  layout?: any,
  theme?: any,
  sunray?: any,
  wordpress?: any,
  router?: any,
  systemComponents?: any,
}

export const initialState: RootState = {
  applicationConfig: applicationConfigInitialState,
  systemComponents: systemComponentsInitialState,
};

export const reducer: any = {
  applicationConfig: applicationConfigReducer,
  systemComponents: systemComponentsReducer,
  router: routerReducer,
}
