import { Action } from '@ngrx/store';

import { PayloadAction } from '../payload/index'

import { typeFor } from './type-for'

export const sliceActions = {
  INIT: 'INIT',
  INITIALIZED: 'INITIALIZED',
  LOAD: 'LOAD',
  LOAD_FAIL: 'LOAD_FAIL',
  LOAD_SUCCESS: 'LOAD_SUCCESS',
  PATCH: 'PATCH',
  UPDATE: 'UPDATE',
  UPDATE_SUCCESS: 'UPDATE_SUCCESS'
};

export class SliceAction extends PayloadAction implements Action {
  protected actionName = '';

  constructor(public slice: string, public payload?: any) {
    super(payload);
  }

  get type() {
    return typeFor(this.slice, this.actionName);
  }

  get verb() {
    return this.actionName;
  }
}

export class Init extends SliceAction {
  protected actionName: string = sliceActions.INIT;
}

export class Initialized extends SliceAction {
  protected actionName: string = sliceActions.INITIALIZED;
}

export class LoadFail extends SliceAction {
  protected actionName: string = sliceActions.LOAD_FAIL;
}

export class LoadSuccess extends SliceAction {
  protected actionName: string = sliceActions.LOAD_SUCCESS;
}

export class Patch extends SliceAction {
  protected actionName: string = sliceActions.PATCH;

  constructor(public slice: any, path: string[], val: any) {
    super(slice, { path, val });
  }
}

export class Update extends SliceAction {
  protected actionName: string = sliceActions.UPDATE;

  constructor(public slice: any, path: string[], val: any) {
    super(slice, { path, val });
  }
}
