import * as _ from 'lodash';

import {
  ApplicationConfigActionTypes,
  ApplicationConfigActionsUnion,
} from './actions';

import {
  iApplicationConfigState
} from './state';

export function applicationConfigReducer(
  state: iApplicationConfigState,
  action: ApplicationConfigActionsUnion
): iApplicationConfigState {
  var deltaState = {};

  switch (action.type) {
    case 'ROUTER_NAVIGATION':
      deltaState = {
        route:  action.payload.routerState
      };
      return _.assign({}, state, deltaState); 
    case ApplicationConfigActionTypes.LAUNCH:
      deltaState = {
        launched: true
      };
      return _.assign({}, state, deltaState); 
    case ApplicationConfigActionTypes.SET_PRIMARY_ENTITY:
      deltaState = {
       primaryEntity: action.payload,
      };
      return _.assign({}, state, deltaState);
    case ApplicationConfigActionTypes.SET_RESOURCE_TYPE:
      deltaState = {
        resourceType: action.payload,
      };
      return _.assign({}, state, deltaState);
    default:
      return state;
  }
}
