import * as _ from 'lodash';

import { createFeatureSelector, createSelector, MemoizedSelector } from '@ngrx/store';

import { RouterState } from './state';

export const selectRouter = createFeatureSelector<any> ('router');

let selectState = (state: RouterState) => {
  if(state && state.state) {
    return state.state;
  }
  else {
    return null;
  }
};

export const selectRouterState = createSelector(
  selectRouter,
  selectState,
)

let selectParamId = (state: any) => {
  if(state) {
    return _.get(state, 'params.id');
  }
  else {
    return null;
  }
}

export const selectRouteParamId = createSelector(
  selectRouterState,
  selectParamId,
)

export const routerSelectors = {
  selectState: selectRouterState,
  selectRouteParamId: selectRouteParamId,
}
