import * as _ from 'lodash'

import { Injectable }                  from '@angular/core'
import { RouterStateSnapshot, Params } from '@angular/router'

import { StoreModule, ActionReducerMap } from '@ngrx/store'

import {
  RouterStateSerializer,
} from '@ngrx/router-store'

import { iRouterStateUrl } from './state'


// https://github.com/ngrx/platform/blob/master/docs/router-store/api.md#navigation-actions
// https://ngrx.io/guide/router-store/configuration

@Injectable()
export class RouterCustomSerializer
  implements RouterStateSerializer<iRouterStateUrl> {

  serialize(routerState: RouterStateSnapshot): iRouterStateUrl {
    let route = routerState.root

    let params = {}
    while (route.firstChild) {
      Object.assign(params, route.params)
      route = route.firstChild
    }
    Object.assign(params, route.params)

    let segments = <any>_.get(routerState, 'root.children[0]._urlSegment.segments')

    const {
      url,
      root: { queryParams },
    } = routerState

    return { url, params, segments, queryParams }
  }
}

