import { pipe }   from 'rxjs';
import { filter } from 'rxjs/operators';

import {
  select, createFeatureSelector,
  createSelector, MemoizedSelector
} from '@ngrx/store';

import { routerSelectors }        from '../router';

import { iSystemComponentsState } from './state';

export const selectSystemComponents = createFeatureSelector<any>('systemComponents')


let selectActiveComponents = (state: iSystemComponentsState) => {
  return state.activeComponents
};

export const selectSystemComponentsActiveComponents = createSelector(
selectSystemComponents,
selectActiveComponents,
);

export const systemComponentsSelectors = {
  slice: selectSystemComponents,
  activeComponents: selectSystemComponentsActiveComponents
}
